.title {
  text-align: center;
  width: 60%;
  border-bottom: 1px solid rgba(128, 128, 128, 0.5);
  margin: 0.67em auto 1em auto;
}
.article-pane {
  margin: 0 10px;
}
@media (min-width: 768px) {
  .article-pane {
    margin: 0 20px 0 25%;
  }
}
@media (min-width: 1440px) {
  .article-pane {
    margin: 0 0 0 350px;
  }
}



